<template>
  <div>
    <div class="nav">
      <van-nav-bar title="收藏" left-text="返回" left-arrow @click-left="onClickLeft">
        <template #right>
          <div v-if="allLove.length > 0">
            <span @click="taggleState('isEdit')" v-show="!isEdit" style>编辑</span>
            <span @click="taggleState('isEdit')" v-show="isEdit">完成</span>
          </div>
        </template>
      </van-nav-bar>
    </div>

    <template v-if="allLove.length > 0">
      <van-list
        :immediate-check="false"
        @load="onLoad"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
      >
        <van-swipe-cell :disabled="!isEdit" v-for="(item,index) in allLove" :key="index">
          <div class="item border-bottom" @click="goDetail(item)">
            <div class="left">
              <van-checkbox v-show="isEdit" v-model="item.ischecked"></van-checkbox>
            </div>
            <div class="img">
              <img v-lazy="item.smallImg" :src="item.smallImg" alt />
            </div>
            <div class="content">
              <p class="title">
                {{item.name}}
                <span class="price">￥{{item.price}}</span>
              </p>
              <p class="time">{{item.enname}}</p>
            </div>
          </div>

          <template #right>
            <van-button
              @click="notlikeOne(item)"
              :disabled="!item.ischecked"
              square
              text="取消收藏"
              type="danger"
              class="delete-button"
            />
          </template>
        </van-swipe-cell>
      </van-list>
      <div :class="{'is-open':isEdit}" class="bottom">
        <div class="left">
          <van-checkbox @click="selAll" v-model="checkedAll">全选</van-checkbox>
        </div>
        <div>
          <van-button
            @click="notlikeAll"
            :disabled="!checkedAll"
            round
            type="default"
            color="#ff4121"
            size="small"
          >全部不爱了</van-button>
        </div>
      </div>
    </template>

    <template v-else>
      <div class="tag" v-show="ready">快去收藏吧</div>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import Api from "../api/api";
import Util from "../util/utils";
export default {
  data() {
    return {
      loading: false,
      // 全选状态
      checkedAll: false,
      ready: false
    };
  },
  created() {
    this.$store.commit("loveModule/empty");
    //   去请求所有收藏商品
    let tokenString = Util.getCookies(this);

    Api.findAllLike({ tokenString }).then(res => {
      if (res.data.code == 2000) {
        //   处理一下数据 ==> 加上ischecked字段
        let data = res.data.result;
        data.forEach(v => {
          v.ischecked = false;
        });

        // 推入allLoveData里面
        this.$store.commit("loveModule/changeData", {
          key: "allLoveData",
          data
        });
        // 初始化7个
        this.$store.commit("loveModule/loadingOne", { t: this });
      }
    });
  },
  mounted() {
    setTimeout(() => {
      this.ready = true;
    }, 1000);
  },
  computed: {
    ...mapState("loveModule", ["finished", "isEdit", "allLove"]),
    ...mapGetters("loveModule", ["isCheckedAll"])
  },
  watch: {
    isCheckedAll() {
      this.checkedAll = this.isCheckedAll;
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onLoad() {
      // 一轮加七个
      this.$store.commit("loveModule/loadingOne", { t: this });
    },
    taggleState(key) {
      this.$store.commit("loveModule/taggleState", { key });
    },
    selAll() {
      this.$store.commit("loveModule/changeCheckedAll", {
        bool: !this.checkedAll
      });
    },
    notlikeOne(item) {
      //   API 取消收藏一个，并且把数据删除一个
      let tokenString = Util.getCookies(this);

      Util.showLoading(this, "取消收藏中...");
      Api.notlike({ ...item, tokenString }).then(res => {
        Util.clearLoading(this);
        if (res.data.code == 900) {
          this.$toast(res.data.msg);
          this.$store.commit("loveModule/delOne", { item });
        }
      });
    },
    notlikeAll() {
      //   API 遍历取消收藏所有，并且把数据清空
      this.$toast("暂无此Api");
    },
    goDetail(item) {
      if (!this.isEdit) {
        this.$router.push({ name: "Detail", params: { pid: item.pid } });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/util.scss";
.tag {
  height: 500px;
  line-height: 500px;
  text-align: center;
  font-size: 20px;
}
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}
.nav {
  padding-top: 20px;
}

.van-list {
  height: 561px;
  overflow-y: auto;
}
.item {
  width: 100%;
  height: 90px;
  display: flex;
  padding: 5px 10px;
  box-sizing: border-box;
  // margin-top: 5px;
  //   align-items: center;
  .left {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .img {
    width: 80px;
    img {
      width: 100%;
      display: block;
      border-radius: 10px;
    }
  }
  .content {
    width: calc(80% - 80px);
    padding: 0 10px;

    .title {
      font-size: 18px;
      height: 40px;
      line-height: 40px;
      box-sizing: border-box;
      display: flex;
      .price {
        color: #f95151;
        margin-left: 15px;
      }
    }
    .time {
      font-size: 18px;
      height: 40px;
      line-height: 40px;
    }
  }
}
.bottom {
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  padding: 5px 10px;
  bottom: -100%;
  left: 0;
  display: flex;
  justify-content: space-between;
  transition: bottom 0.2s ease;
  .left {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.is-open {
  bottom: 0;
}

.delete-button {
  height: 100%;
}
</style>